import React, { useState } from "react";
import Button from "../../components/Button/Button";
import Modal from '../../components/Modal/Modal';
import classNames from "./Buttons.module.css";
import {connect} from 'react-redux';
import * as reduxFunctions from '../../store/actions/index';

const Buttons = (props) => {
  const allInputs = document.querySelectorAll('#inputs');
  const digitsU = ["1", "2", "3"];
  const [digitsL, setDigitsL] = useState(["1", "2", "3"]);
  const [upperDigit, setUpperDigit] = useState(3);
  const [lowerDigit, setLowerDigit] = useState(3);
  const [activeClassTop, setActiveClassTop] = useState({
    0:false,
    1:false,
    2:true
  })
  const [activeClassBottom, setActiveClassBottom] = useState({
    0:false,
    1:false,
    2:true
  })
  const [generateOnStart, setGenerateOnStart] = useState(true);

  const [randomBtnModal, setRandomBtnModal] = useState(false);
  const triggerModal = () => {
    setRandomBtnModal(true);
  };
  const closeModal = () => {
    setRandomBtnModal(false);
  };
  const falsyObject = {
    0:false,
    1:false,
    2:false
  }
  const upDigit = (e, i) => {
    setUpperDigit(Number(e.target.innerText));
    const iToStr = i.toString();
    setActiveClassTop({
      ...falsyObject,
      [i.toString()] : !activeClassTop[iToStr]
    })
    switch (i) {
      case 0:
        setDigitsL(['1'])
        setLowerDigit(1)
        setActiveClassBottom({
          ...falsyObject,
          0: true
        })
        break;
      case 1:
        setDigitsL(['1', '2'])
        setLowerDigit(2)
        setActiveClassBottom({
          ...falsyObject,
          1: true
        })
        break;
      case 2:
        setDigitsL(['1', '2', '3'])
        break;
      default:
        break;
    }
  };

  const lowDigit = (e, i) => {
    setLowerDigit(Number(e.target.innerText));
    const iToStr = i.toString();
    setActiveClassBottom({
      ...falsyObject,
      [i.toString()] : !activeClassBottom[iToStr]
    })
  };
  const starting = () => {
    let inputCheck = true;
    let emptyCheck = true;
    const tL = props.tR.join('').length;
    const bL = props.bR.join('').length;

    if(tL < bL){
      inputCheck = false;
    }

    if(tL <= 0 || bL <= 0){
      emptyCheck = false;
    }

    if(inputCheck && emptyCheck){
      allInputs.forEach(c=>{
        c.disabled = true;
      })
      setUpperDigit(tL);
      setLowerDigit(bL);
      props.startMulti(props.tR, props.bR);
      if(!generateOnStart){
        setGenerateOnStart(true);
      }
    }else if(!inputCheck){
      alert('You can\'t add less numbers in the bottom then top!')
    }else if(!emptyCheck){
      alert('You can\'t leave top or bottom fields empty!')
    }
  }

  const ending = () => {
    props.endMulti();
  }

  const setTopAndBelowItems = () => {
    setRandomBtnModal(false);
    setGenerateOnStart(false);
    props.random(upperDigit, 'topRow');
    props.random(lowerDigit, 'bottomRow');
  };

  const nextSteps = () => {
    props.countSteps(props.c, upperDigit, lowerDigit, props.results);
    props.carry(props.c, props.carries, upperDigit, lowerDigit);
    props.result(props.c, upperDigit, lowerDigit);
  }

  const prevSteps = () => {
    props.prevSteps(props.c, upperDigit, lowerDigit, props.results);
  }

  return (
    <>
    <Modal show={randomBtnModal} close={closeModal}>
        <div className={classNames.randomDigitContainer}>
          <h1>How many digit you want in top?</h1>
          <div className={classNames.selections}>
            {digitsU.map((c, i) => {
              return (
                <div
                  key={c}
                  className={`${classNames.selectOption} ${activeClassTop[i.toString()] ? classNames.active : null }`}
                  onClick={(e) => {
                    upDigit(e, i);
                  }}
                >
                  {c}
                </div>
              );
            })}
          </div>
        </div>
        <div className={classNames.randomDigitContainer}>
          <h1>How many digit you want in Bottom?</h1>
          <div className={classNames.selections}>
            {digitsL.map((c, i) => {
              return (
                <div
                  key={c}
                  className={`${classNames.selectOption} ${activeClassBottom[i.toString()] ? classNames.active : null }`}
                  onClick={(e) => {
                    lowDigit(e, i);
                  }}
                >
                  {c}
                </div>
              );
            })}
          </div>
        </div>
        <div className={classNames.generate}>
          <Button btnClick={setTopAndBelowItems}>
            <span>Generate</span>
            <i className="fas fa-check"></i>
          </Button>
        </div>
      </Modal>
      <div className={classNames.container}>
        <div className={classNames.inner}>
          {/* props.btnHide props.nextBtn */}
          <button onClick={triggerModal} disabled={!props.btnHide}>
            <i className="fas fa-random"></i>
          </button>
          <button onClick={prevSteps} disabled={props.btnHide || props.nextBtn ? true : false}>
            <i className="fas fa-fast-backward"></i>
          </button>
          <button onClick={starting} disabled={!props.btnHide}>
            <i className="fas fa-play"></i>
          </button>
          <button onClick={ending} disabled={props.btnHide}>
            <i className="fas fa-pause"></i>
          </button>
          <button onClick={nextSteps} disabled={props.btnHide || props.nextBtn ? true : false}>
            <i className="fas fa-fast-forward"></i>
          </button>
        </div>
      </div>
    </>
  );
};
const mapStatetoProps = state => {
  return{
      tR: state.number.topRow,
      bR: state.number.bottomRow,
      c: state.number.counter,
      nextBtn: state.number.nextBtnHide,
      btnHide: state.number.btnHide,
      fR: state.result.finalResult,
      carries: state.multi.answer.carry,
      results: state.multi.answer
  }
}

const mapDispatchtoProps = dispatch => {
  return{
      random: (d, fn) => dispatch(reduxFunctions.randomNumberGenerate(d, fn)),
      startMulti: (t, b, d1, fn1, d2, fn2) => dispatch(reduxFunctions.startMultiplication(t, b, d1, fn1, d2, fn2)),
      endMulti: () => dispatch(reduxFunctions.endMultiplication()),
      countSteps: (p, u, d, r) => dispatch(reduxFunctions.stepCounter(p, u, d, r)),
      prevSteps: (c, u, d, r) => dispatch(reduxFunctions.prevCounter(c, u, d, r)),
      carry: (p, c, u, d) => dispatch(reduxFunctions.carryAddition(p, c, u, d)),
      result: (p, u, d, r) => dispatch(reduxFunctions.resultAddition(p, u, d, r))
  }
}

export default connect(mapStatetoProps, mapDispatchtoProps)(Buttons);