import React from 'react';
import Buttons from '../ActionButtons/Buttons';
import ExplainationBoard from '../ExplainationBoard/ExplainationBoard';
import Board from '../NumberBoard/Board';
import classNames from './Layout.module.css';
import Header from '../Header/Header';

const Layout = () => {
    return (
        <>
            <div className={classNames.container}>
                <Header />
                <div className={classNames.inner}>
                    <div className={classNames.board_container}>
                        <Board/>
                    </div>
                    <div className={classNames.explainer_container}>
                        <ExplainationBoard/>
                    </div>
                    <div className={classNames.number_pad_container}>

                    </div>
                </div>
                <Buttons/>
            </div>
        </>
    );
};
export default Layout;