import * as actionTypes from '../actions/actions';

const initState = {
    topRow: ['','',''],
    bottomRow: ['','',''],
    boardTitle: {
        title: '',
        show: false
    },
    counter: 0,
    nextBtnHide: false,
    resutltsRow: 5,
    inputDisable: false,
    btnHide: true
}

const reducer = (state = initState, actions) => {
    if(state.counter === 19){
        return{
            ...state,
            counter: 0,
            nextBtnHide: true
        }
    }

    if(actions.type === actionTypes.COUNTERRESULT){
        if(actions.upperDigit === 1 && actions.lowerDigit === 1){
            if(state.counter === 1){
                return{
                    ...state,
                    counter: 18,
                }
            }
        }
        if(actions.upperDigit === 2 && actions.lowerDigit === 1){
            if(state.counter === 2){
                return{
                    ...state,
                    counter: 18,
                }
            }
        }
        if(actions.upperDigit === 2 && actions.lowerDigit === 2){
            switch (state.counter) {
                case 2:
                    return{
                        ...state,
                        counter: 3,
                    }
                case 6:
                    return{
                        ...state,
                        counter: 11,
                    }
                case 16:
                    return{
                        ...state,
                        counter: 18,
                    }
            
                default:
                    break;
            }
        }
        if(actions.upperDigit === 3 && actions.lowerDigit === 1){
            if(state.counter === 3){
                return{
                    ...state,
                    counter: 18,
                }
            }
        }
        if(actions.upperDigit === 3 && actions.lowerDigit === 2){
            if(state.counter === 7){
                return{
                    ...state,
                    counter: 11,
                }
            }else if(state.counter === 17){
                return{
                    ...state,
                    counter: 18,
                }
            }
        }
    }
    if(actions.type === actionTypes.COUNTERPREV){
        if(actions.upperDigit === 1 && actions.lowerDigit === 1){
            if(state.counter === 18){
                return{
                    ...state,
                    counter: 0,
                }
            }
        }
        if(actions.upperDigit === 2 && actions.lowerDigit === 1){
            if(state.counter === 18){
                return{
                    ...state,
                    counter: 1,
                }
            }
        }
        if(actions.upperDigit === 2 && actions.lowerDigit === 2){
            switch (state.counter) {
                case 3:
                    return{
                        ...state,
                        counter: 1,
                    }
                case 11:
                    return{
                        ...state,
                        counter: 5,
                    }
                case 18:
                    return{
                        ...state,
                        counter: 15,
                    }
            
                default:
                    break;
            }
        }
        if(actions.upperDigit === 3 && actions.lowerDigit === 1){
            if(state.counter === 18){
                return{
                    ...state,
                    counter: 2,
                }
            }
        }
        if(actions.upperDigit === 3 && actions.lowerDigit === 2){
            if(state.counter === 11){
                return{
                    ...state,
                    counter: 6,
                }
            }else if(state.counter === 18){
                return{
                    ...state,
                    counter: 16,
                }
            }
        }
    }

    const randomNumberGenerate = (digit) => {

        let randomNumArray;
        const randomNum1 = Math.floor(Math.random() * (9)) + 1;
        const randomNum2 = Math.floor(Math.random() * (9)) + 1;
        const randomNum3 = Math.floor(Math.random() * (9)) + 1;
        const randomNumString1 = randomNum1.toString();
        const randomNumString2 = randomNum2.toString();
        const randomNumString3 = randomNum3.toString();

        switch (digit) {
            case 3:
                randomNumArray = [randomNumString1, randomNumString2, randomNumString3];
                break;
            case 2:
                randomNumArray = ['', randomNumString1, randomNumString2];
                break;
            case 1:
                randomNumArray = ['', '', randomNumString1];
                break;

            default:
                randomNumArray = [randomNumString1, randomNumString2, randomNumString3];
                break;
        }
        
        return randomNumArray
    }

    switch (actions.type) {
        case actionTypes.RANDOM_NUMBER:
            let randomNumArray = randomNumberGenerate(actions.digitCount);
            return{
                ...state,
                [actions.rowType]: randomNumArray
            }
        case actionTypes.INPUT_NUMBER:
            return{
                ...state,
                [actions.rowPosition]: actions.newNumbers
            }
        case actionTypes.START:
            const t = state.topRow.join('')
            const b = state.bottomRow.join('')
            
            return{
                ...state,
                boardTitle: {
                    title: `We will multiply <span>${state.topRow[0]}${state.topRow[1]}${state.topRow[2]}</span> x <span>${state.bottomRow[0]}${state.bottomRow[1]}${state.bottomRow[2]}</span>. The answer is <span>${t * b}</span>, but that doesnt matter. We dont care about the answer here; we care only about how to get it. <span>Click Next</span>`,
                    show: true
                },
                nextBtnHide: false,
                resutltsRow: b.length,
                inputDisable: true,
                btnHide: false
            }
        case actionTypes.END:
            return{
                ...state,
                topRow: ['','',''],
                bottomRow: ['','',''],
                boardTitle: {
                    ...state.boardTitle,
                    show: false
                },
                counter: 0,
                resutltsRow: 5,
                inputDisable: false,
                btnHide: true
            }
        case actionTypes.COUNTER:
            return{
                ...state,
                counter: state.counter + 1,
            }
        case actionTypes.COUNTERPREV:
            if(state.counter === 0){
                return{
                    ...state,
                    topRow: ['','',''],
                    bottomRow: ['','',''],
                    boardTitle: {
                        ...state.boardTitle,
                        show: false
                    },
                    counter: 0,
                    resutltsRow: 5,
                    inputDisable: false,
                    btnHide: true
                }
            }else{
                return{
                    ...state,
                    counter: state.counter - 1,
                }
            }
        default:
            return state
    }
}

export default reducer;